    p.mt-5 El concepto general de bases de datos segn la RAE es el conjunto de datos organizado de tal modo que permita obtener con rapidez diversos tipos de informacin (RAE ,2001). De acuerdo con esta definicin, una hoja de clculo de Excel puede considerarse una base de datos, o un conjunto de archivos debidamente organizados, o la lista de nombres y telfonos que est en nuestros smartphones. En principio, es correcto llamarle base de datos a estos ejemplos, sin embargo, en el contexto de desarrollo de software, se referir a ese conjunto de informacin que puede ser almacenada en grandes cantidades de forma organizada y es gestionada a travs de un #[strong Sistema de Gestin de Bases de Datos] (SGBD).
    p.mt-4 Para organizar y definir la informacin de forma sistemtica, las bases de datos tambin deben poder almacenar una descripcin precisa de los datos que contiene conocida como #[strong metadatos] a los que se le relaciona el tipo de informacin que es conceptualmente guardada (es decir si se agrega una explicacin de la naturaleza del dato en la empresa) se da origen a lo que conoce como #[strong diccionario de datos o catlogo de datos].
