    .titulo-segundo.mt-5
      #t_1_2.h4 1.2  Tipos de bases de datos
    p.mt-4 Las bases de datos han evolucionado a lo largo del tiempo, usando diferentes tipos de modelos o enfoques, se ha comprobado que algunos de ellos tienen mejor desempeo en distintos tipos de aplicacin. La estructura en la que se configuran los datos al interior de las bases de datos es una forma de clasificacin:
    TabsB.color-secundario.mb-5
      .py-4.py-md-5(titulo="Bases de datos con estructura jerrquica" :icono="require('@/assets/template/tema-1-6.svg')")
        .row
          .col-12.col-lg-6
            h4 Bases de datos con estructura jerrquica
            p En las primeras bases de datos empleadas son las de estructura jerrquica. Las relaciones entre registros forman una estructura en rbol, en la actualidad un ejemplo de estas bases de datos es la base de datos LDAP usada en la actualidad para el sistema de directorio de datos tales como usuario, dispositivos, nombres, contraseas, direcciones etc., dentro de una red de computadores.
          .col-8.col-lg-6.offset-2.offset-lg-0
            figure.mt-5
              img(src="@/assets/template/tema-1-11.png" alt="Texto que describa la imagen")
              p.mt-3(style='font-size: 10px; word-wrap: break-word;') Nota. Tomada de https://es.wikipedia.org/wiki/Sistema_de_gesti%C3%B3n_de_bases_de_datos#/media/Archivo:DB_jerarquica.png
      .py-4.py-md-5(titulo="Bases de datos con estructura en red" :icono="require('@/assets/template/tema-1-7.svg')")
        .row
          .col-12.col-lg-6
            h4 Bases de datos con estructura en red
            p Esta estructura es un poco ms compleja que la anterior porque contiene otras relaciones de las jerrquicas admitiendo relaciones de cada registro con otros, de manera tal que puede seguir distintos caminos para acceder a la informacin.
          .col-8.col-lg-6.offset-2.offset-lg-0
            figure.mt-5
              img(src="@/assets/template/tema-1-12.svg" alt="Texto que describa la imagen")
      .py-4.py-md-5(titulo="Bases de datos con estructura relacional" :icono="require('@/assets/template/tema-1-8.svg')")
        .row
          .col-12.col-lg-6
            h4 Bases de datos con estructura relacional
            p Estas bases de datos son multipropsito, lo que las hace las ms extendidas en la industria y ms empleadas, pero son las ms complejas de aprender; por lo que solo se abordar el diseo e implementacin de este tipo de bases de datos. Se basan en tablas como las que se present en la figura "Base de datos de llamadas telefnicas" y en la relacin de los datos de unas tablas con otras.
          .col-8.col-lg-6.offset-2.offset-lg-0
            figure.mt-5
              img(src="@/assets/template/tema-1-13.png" alt="Texto que describa la imagen")
      .py-4.py-md-5(titulo="Bases de datos con estructura multidimensional" :icono="require('@/assets/template/tema-1-9.svg')")
        .row
          .col-12.col-lg-6
            h4 Bases de datos con estructura multidimensional
            p Es la extensin de las bases de datos de estructuras relacionales, como se vio en las relacionales, se basan en estructuras bidimensionales (tablas), mientras que la multidimensional en cubos (tres dimensiones), o ms complejas con N dimensiones.
          .col-8.col-lg-6.offset-2.offset-lg-0
            figure.mt-5
              img(src="@/assets/template/tema-1-14.png" alt="Texto que describa la imagen")
              p.mt-5.h6.text-center Estructura de datos de cubo
      .py-4.py-md-5(titulo="Bases de datos con estructura orientada a objetos" :icono="require('@/assets/template/tema-1-10.svg')")
        .row
          .col-12.col-lg-6
            h4 Bases de datos con estructura orientada a objetos
            p Una base de datos orientada a objetos es un sistema de gestin de base de datos mediante el cual representamos la informacin en forma de objetos que son utilizados en programacin orientada a objetos #[strong (Kyocera, 2021)].
          .col-8.col-lg-6.offset-2.offset-lg-0
            figure.mt-5
              img(src="@/assets/template/tema-1-15.png" alt="Texto que describa la imagen")
    p.mt-4 Cada tipo de bases de datos tiene un mbito de aplicacin en el que su desempeo es mejor. Por ejemplo, las bases de datos jerrquicas tienen un mejor desempeo en operaciones de consulta de datos puntuales, mientras que las de datos relacionales son precisas para garantizar la calidad de los datos y la no repeticin de los mismos, aunque no muy optimizadas para hacer consultas, y, finalmente, las multidimensionales son muy tiles para anlisis estadstico de datos histricos de volmenes inmensos de datos. Aparte del tipo de datos, existe otro tipo de clasificaciones ya no segn la estructura en que se almacenan los datos, sino de acuerdo con la naturaleza de los datos.
    .titulo-segundo.mt-5
      #t_1_3.h4 1.3  Clasificacin de bases de datos
    p.mt-4 Segn la naturaleza de los datos que se almacenan en la base de datos, se pueden clasificar; la siguiente tabla muestra una compilacin de estas clasificaciones.
