                        .col-10.vineta-lm.m-0
                          p.mb-2 Son bases de datos, cuyos datos son histricos, es decir ya no se pueden modificar, se usan comnmente para estudiar el comportamiento de los datos a travs del tiempo.
                    li.mb-0.d-block.mt-1
                      .row
                        .col-1
                          i.fas.fa-angle-right.color-azul
                        .col-11.vineta-lm
