                    li.mb-0.d-block.mt-1
                      .row
                        .col-1
                          i.fas.fa-angle-right.color-azul
                        .col-10.vineta-lm
                          p.mb-2 Muchas de estas bases de datos se almacenan en cubos para su anlisis, se les conoce como OLAP es el acrnimo en ingls de procesamiento analtico en lnea (On-Line Analytical Processing).
          tr.text-center
            th Bases de datos dinmicas
            td 
              .row
                .col-sm.mb-5.mb-sm-0
                  ul.lista-ul
                    li.mb-0.d-block
                      .row
                        .col-1
                          i.fas.fa-angle-right.color-azul
                        .col-10.vineta-lm
                          p.mb-2 Estos datos se almacenan y pueden ser modificados, agregados, borrados y consultados en cualquier momento, por ejemplo: un sistema de facturacin o el sistema Sofiaplus del SENA.
                    li.mb-0.d-block.mt-1
                      .row
                        .col-1
                          i.fas.fa-angle-right.color-azul
                        .col-10.vineta-lm
                          p.mb-2 A estos sistemas se les denomina transaccionales, porque cada operacin de guardar, borrar o editar se configura como una transaccin. OLTP es la sigla en ingls de Procesamiento de Transacciones en Lnea (On-Line Transaction Processing).
          tr(style="background-color: transparent; border: solid 1px #e8e8e8;").text-center
            th(rowspan='2') Segn el contenido
