                .col-sm.mb-5.mb-sm-0
                  ul.lista-ul
                    li.mb-0.d-block
                      .row
                        .col-1
                          i.fas.fa-angle-right.color-azul
                        .col-10.vineta-lm
                          p.mb-2 Permiten la indexacin a texto completo, y en lneas generales realizar bsquedas ms potentes.
          tr.text-center
            th Base de datos deductivos
            td 
              .row
                .col-sm.mb-5.mb-sm-0
                  ul.lista-ul
                    li.mb-0.d-block
                      .row
                        .col-1
                          i.fas.fa-angle-right.color-azul
                        .col-10.vineta-lm
                          p.mb-2 Un sistema de base de datos deductivos, es un sistema de base de datos, pero con la diferencia de que permite hacer deducciones a travs de inferencias. Se basa principalmente en reglas y hechos que son almacenados en la base de datos. Tambin las bases de datos deductivas son llamadas base de datos lgica, a raz de que se basan en lgica matemtica.
    p.mt-4 Pueden existir otras clasificaciones orientadas al mbito de uso, sin embargo, se presentan las ms comunes en el ejercicio del desarrollo de software. Las bases de datos relacionales es de obligatorio dominio en cualquiera de los casos, ya que los conceptos de esta son reutilizables en casi todos los otros tipos.
    .titulo-segundo.mt-5
